import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const StyledNavItem = styled.button`
  line-height: 1;
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  letter-spacing: 0;
  color: currentColor;
  border: 0;
  background: none;

  &:focus {
    outline: none;
  }
`;

const NavItem = ({
  className, current, children, ...props
}) => (
  <StyledNavItem className={`${current ? 'nav__item--current ' : ''}${className}`} {...props}>
    {children}
  </StyledNavItem>
);

NavItem.propTypes = {
  className: PropTypes.string,
  current: PropTypes.bool,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

NavItem.defaultProps = {
  className: '',
  current: false,
};


export default NavItem;
